body
  @include scrollbars($scrl-bar_width, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
  overflow-x: hidden
  position: relative
  height: 100%
  background-color: #fff
  &.overflow
    overflow: hidden
    &:after
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background-color: rgba(0,0,0,.6)
      z-index: 2
.ccontainer
  width: 100%
  margin: 0 auto
  max-width: $container-width
  padding: $container-padding



