.cheader
  position: fixed // tmp fixed
  top: 0
  left: 0
  width: 100%
  z-index: 10
  background-color: #fff
  border-bottom: 1px solid transparent
  transition: border-color .3s
  .ccontainer
    max-width: 1440px
  &__top
    padding: 16px 0
    background: $gradient
    @media (max-width: 420px)
      padding: 20px 0
    .ccontainer
      display: flex
      align-items: center
      justify-content: center
      position: relative
      z-index: 1
      .cheader-link-wrapper
        display: inline-block
        text-decoration: none
        @media (max-width: 420px)
          display: block
          width: 100%
          background-color: #fff
          color: $pink-color
          text-decoration: none
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          text-transform: uppercase
          text-align: center
          padding: 10px 5px
          border-radius: 100px
          transition: background-color .3s, color .3s
      .cheader-mobile
        display: none
        @media (max-width: 420px)
          display: inline-block
          color: $pink-color
          font-size: 11px
          line-height: 20px
      .cheader-txt
        font-family: $default-font
        font-weight: 700
        font-size: 15px
        line-height: 21px
        color: $txt-color
        letter-spacing: .05em
        text-transform: uppercase
        margin-right: 31px
        @media (max-width: 420px)
          color: $pink-color
          margin-right: 0
          font-size: 11px
          line-height: 20px
      .cheader-link
        display: inline-block
        background-color: #fff
        color: $pink-color
        text-decoration: none
        font-family: $default-font
        font-weight: 700
        font-size: 12px
        text-transform: uppercase
        text-align: center
        padding: 12px 24px
        border-radius: 100px
        margin-right: 35px
        transition: background-color .3s, color .3s
        letter-spacing: .02em
        @media (max-width: 420px)
          display: none
        &:hover
          color: #fff
          background-color: $pink-color
    .cheader-close
      position: absolute
      top: calc(50% - 12px)
      right: 35px
      font-size: 24px
      line-height: 24px
      color: rgba($txt-color, .5)
      transition: color .3s
      z-index: 1
      @media (max-width: 420px)
        top: -18px
        right: 2px
        font-size: 18px
        line-height: 18px
      &:hover
        color: $txt-color
  &__main
    padding: 15px 0
    transition: padding-top .3s, padding-bottom .3s
    @media (max-width: 1024px)
      position: relative
    @media (max-width: 420px)
      padding: 10px 0
    .ccontainer
      display: flex
      align-items: center
      justify-content: flex-start
    .logo-link
      display: block
      flex: 0 0 auto
      width: auto
      @media (max-width: 1024px)
        max-width: 100px
      .logo-img
        display: block
        width: auto
        max-width: 100%
        @media (max-width: 420px)
          max-width: 90px
    .mmenu
      flex: 0 0 auto
      width: auto
      margin-left: 40px
      @media (max-width: 1200px)
        margin-left: 30px
      @media (max-width: 1024px)
        position: absolute
        right: 0
        top: 100%
        width: 320px
        height: calc(100vh - 100% - var(--headerhalf-height))
        background-color: #fff
        transform: translateX(100%)
        transition: transform .3s
        overflow-y: scroll
        &.show
          transform: translateX(0)
      .mmenu-list
        display: flex
        align-items: center
        justify-content: flex-start
        @media (max-width: 1024px)
          flex-direction: column
          margin-top: 1px
        &__item
          display: block
          width: auto
          margin-left: 40px
          padding: 10px 0
          position: relative
          @media (max-width: 1200px)
            margin-left: 30px
          @media (max-width: 1024px)
            display: flex
            align-items: center
            justify-content: flex-start
            flex-wrap: wrap
            width: 100%
            margin-left: 0
            padding: 10px 20px
            border-bottom: 1px solid #eee
          &:nth-of-type(1)
            margin-left: 0
            @media (max-width: 1024px)
              border-top: 1px solid #eee
          &:hover
            .mmenu-list__sublink
              color: #000
            .submenu-list
              @media (min-width: 1200px)
                display: block !important
          &.active
            .mmenu-list__sublink
              color: #000
              transform: scale(1,-1)
              bottom: 0
        &__link
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          line-height: 16px
          letter-spacing: .045em
          color: #000
          text-transform: uppercase
          text-decoration: none
          @media (max-width: 1024px)
            font-size: 14px
            line-height: 18px
            padding: 5px 0
        &__sublink
          display: inline-block
          color: #c4c4c4
          font-size: 8px
          line-height: 8px
          margin-left: 3px
          position: relative
          bottom: 1px
          transition: color .3s
          text-decoration: none
          @media (max-width: 1024px)
            display: flex
            align-items: center
            justify-content: center
            width: 28px
            height: 28px
            margin-left: auto
            bottom: 2px
            .icon
              width: auto
              height: auto
        .submenu-list
          position: absolute
          top: 100%
          left: 0
          width: auto
          min-width: 300px
          background-color: #fff
          padding: 20px
          border: 1px solid #eee
          @media (max-width: 1024px)
            position: relative
            top: 0
            left: 0
            width: 100%
            min-width: auto
            border: 0
            background-color: transparent
            padding: 15px
          &__item
            display: block
            margin-top: 10px
            @media (max-width: 1024px)
              margin: 15px 0
              &:last-child
                margin-bottom: 0
            &:nth-of-type(1)
              margin-top: 0
          &__link
            display: inline-block
            font-family: $default-font
            font-size: 16px
            line-height: 20px
            text-decoration: none
            color: $txt-color
            position: relative
            @media (max-width: 960px)
              font-size: 15px
            &:before
              content: ''
              display: block
              width: 0
              height: 1px
              background-color: $pink-color
              transition: width .3s
              position: absolute
              bottom: 0
              left: 0
            &:hover
              &:before
                width: 100%
        .personal-block
          display: flex
          align-items: center
          justify-content: center
          flex-wrap: wrap
          margin-top: 15px
          .enter-link
            display: inline-block
            background-color: $pink-color
            border-radius: 100px
            font-family: $default-font
            font-weight: 700
            font-size: 12px
            line-height: 20px
            color: #fff
            text-align: center
            text-transform: uppercase
            text-decoration: none
            padding: 14px 25px
            letter-spacing: .025em
            transition: background-color .3s, color .3s
          .sing-in-link
            display: inline-block
            border: 2px solid $pink-color
            border-radius: 100px
            font-family: $default-font
            font-weight: 700
            font-size: 12px
            line-height: 20px
            color: $pink-color
            text-align: center
            text-decoration: none
            padding: 12px 23px
            margin-left: 25px
            letter-spacing: .025em
            transition: background-color .3s, color .3s
    .profile-links
      flex: 0 0 auto
      width: auto
      margin-left: auto
      display: flex
      align-items: center
      justify-content: flex-start
      @media (max-width: 1024px)
        position: relative
        z-index: 1
      &__item
        display: inline-block
        margin-left: 20px
        &.support-item
          font-size: 28px
          line-height: 28px
          color: #000
          position: relative
          top: -1px
        &.cart-item
          position: relative
          font-size: 25px
          line-height: 28px
          color: #000
          .cart-count
            position: absolute
            top: -5px
            right: -5px
            background-color: $pink-color
            display: flex
            align-items: center
            justify-content: center
            color: #fff
            font-family: $default-font
            font-size: 12px
            line-height: normal
            min-width: 18px
            height: 18px
            border-radius: 50%
      .personal-block
        margin-left: 20px
        @media (max-width: 1024px)
          margin-left: 40px
        .enter-link
          display: inline-block
          background-color: $pink-color
          border-radius: 100px
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          line-height: 20px
          color: #fff
          text-align: center
          text-transform: uppercase
          text-decoration: none
          padding: 14px 25px
          letter-spacing: .025em
          transition: background-color .3s, color .3s
          @media (max-width: 1200px)
            padding: 14px 15px
          &:hover
            background-color: $pink-darkness-color
        .sing-in-link
          display: inline-block
          border: 2px solid $pink-color
          border-radius: 100px
          font-family: $default-font
          font-weight: 700
          font-size: 12px
          line-height: 20px
          color: $pink-color
          text-align: center
          text-decoration: none
          padding: 12px 23px
          margin-left: 25px
          letter-spacing: .025em
          transition: background-color .3s, color .3s
          @media (max-width: 1200px)
            padding: 12px 15px
            margin-left: 10px
          &:hover
            background-color: $pink-color
            color: #fff
    @import "hamburger"
  &.scroll
    border-bottom: 1px solid #eee
    .cheader__main
      padding: 0
      .mmenu
        @media (max-width: 1024px)
          height: calc(100vh - 100%)
  & + main, & + div
    margin-top: var(--header-height)
    @media (max-width: 420px)
      margin-top: calc(var(--header-height) - 20px)
    // transition: margin-top .3s tmp



