@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
.cmain-block
  padding-top: 35px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    @media (max-height: 768px)
      align-items: center
    @media (max-width: 640px)
      position: relative
    &__left-side
      flex: 0 0 30%
      max-width: 30%
      position: relative
      z-index: 2
      @media (max-width: 1024px)
        flex: 0 0 40%
        max-width: 40%
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
        order: 2
        position: initial
    &__right-side
      flex: 0 0 70%
      max-width: 70%
      max-height: 100%
      margin-top: -33px
      @media (max-width: 1024px)
        flex: 0 0 60%
        max-width: 60%
        margin-top: 0
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
        order: 1
        padding-top: 70px
      @media (max-width: 420px)
        padding-top: 80px
      .cmain-slider
        max-height: 505px
        right: -10px
        padding-right: 30px
        @media (max-width: 1024px)
          right: 0
          padding-right: 20px
          max-height: 410px
        @media (max-width: 768px)
          max-height: 320px
          margin-top: 70px
        @media (max-width: 640px)
          padding-right: 0
          max-height: initial
          width: calc(100% + 30px)
          margin: 0 -15px
        @media (max-width: 420px)
          padding-bottom: 24px
        &.swiper-vertical
          .swiper-wrapper
            flex-direction: column
          .swiper-slide
            flex: 0 0 auto
            height: auto
            width: auto
        .slide-link
          display: flex
          align-items: center
          justify-content: center
          width: 86px
          height: 86px
          border-radius: 50%
          background: #FFFFFF
          box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.8)
          color: $pink-color
          position: absolute
          top: calc(50% - 5px)
          left: calc(50% + 2px)
          text-decoration: none
          transition: transform .3s
          @media (max-width: 1024px)
            top: calc(50% - 42px)
            left: calc(50% - 15px)
          @media (max-width: 768px)
            width: 60px
            height: 60px
            top: calc(50% - 30px)
            left: calc(50% - 10px)
            .icon
              font-size: 22px
              line-height: 24px
          @media (max-width: 420px)
            width: 42px
            height: 42px
            top: calc(50% + 16px)
            left: calc(50% - 21px)
            .icon
              font-size: 18px
              line-height: 18px
              position: relative
              &:before
                position: relative
                left: -1px
                top: 1px
          &:hover
            transform: scale(1.1)
          .icon
            position: relative
            left: 3px
            top: -2px
            &:before
              line-height: normal
        .slide-image
          display: block
          width: auto
          max-width: 100%
          margin: 0 0 0 auto
          @media (max-width: 640px)
            padding: 0 15px
        .cmain-pagination
          position: absolute
          top: 0
          right: 0px
          height: 100%
          width: 10px
          display: flex
          flex-direction: column
          align-items: center
          justify-content: center
          z-index: 1
          @media (max-width: 640px)
            position: absolute
            top: auto
            left: 0
            bottom: 0
            flex-direction: row
            height: 10px
            width: 100%
          .swiper-pagination-bullet
            display: block
            width: 10px
            height: 10px
            border-radius: 50%
            background-color: #D9D9D9
            transition: background-color .3s
            margin: 5px 0
            opacity: 10
            @media (max-width: 640px)
              margin: 0 5px
            &.swiper-pagination-bullet-active
              background-color: $pink-color
  &__title
    flex: 0 0 auto
    width: auto
    max-width: 645px
    margin-right: auto
    font-family: Veles, sans-serif
    font-weight: 700
    font-size: 30px
    line-height: 48px
    color: $txt-color
    position: relative
    z-index: 3
    @media (max-width: 768px)
      max-width: 100%
    @media (max-width: 420px)
      font-size: 20px
      line-height: 33px
  &__main-btn
    display: inline-block
    margin-top: 23px
    background-color: $pink-color
    border-radius: 60px
    font-family: $default-font
    font-weight: 700
    font-size: 12px
    line-height: 22px
    letter-spacing: .035em
    text-decoration: none
    text-transform: uppercase
    text-align: center
    color: #fff
    padding: 13px 52px
    transition: background-color .3s
    &:hover
      background-color: $pink-darkness-color
    @media (max-width: 640px)
      display: block
      position: absolute
      top: 0
      left: 0
      margin-top: 10px
      width: 100%
      max-width: 253px
  &__list
    margin-top: 40px
    ul
      margin: 0
      padding: 0
      list-style-type: none
      color: $txt-color
      font-family: $default-font
      font-size: 16px
      line-height: 21px
      letter-spacing: .03em
      @media (max-width: 768px)
        font-size: 14px
        line-height: 20px
      @media (max-width: 640px)
        font-size: 16px
        line-height: 21px
      li
        display: block
        position: relative
        padding-left: 32px
        margin-top: 21px
        @media (max-width: 420px)
          padding-left: 36px
        &:nth-of-type(1)
          margin-top: 0
        &:before
          content: '\e801'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          font-size: 18px
          color: $pink-color
          position: absolute
          top: 1px
          left: -1px
          @media (max-width: 420px)
            left: 2px
  &__social
    margin-top: 85px
    display: flex
    flex-wrap: wrap
    @media (max-width: 768px)
      margin-top: 45px
    @media (max-width: 420px)
      margin-top: 55px
    .social-title
      display: inline-block
      white-space: nowrap
      font-family: $default-font
      font-weight: 700
      font-size: 14px
      line-height: 18px
      letter-spacing: .02em
      color: $pink-color
      @media (max-width: 420px)
        font-size: 16px
        line-height: 21px
        white-space: pre-wrap
    .social-full
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: #000
      font-family: $default-font
      font-weight: 700
      font-size: 20px
      line-height: 26px
      text-decoration: none
      margin-top: 10px
      transition: color .3s
      @media (max-width: 1024px)
        margin-right: 30px
      @media (max-width: 420px)
        width: 100%
        flex: 0 0 100%
        margin-right: 0
        margin-top: 7px
      span
        display: inline-block
        border: 2px solid #343537
        border-radius: 50px
        color: #343537
        font-size: 10px
        line-height: 13px
        text-transform: uppercase
        text-align: center
        padding: 8px 16px
        margin-left: 8px
        letter-spacing: .03em
        transition: color .3s, border-color .3s, background-color .3s
        @media (max-width: 420px)
          flex: 1 1 auto
          padding: 9px 16px
      &:hover
        color: $pink-color
        span
          background-color: $pink-color
          color: #fff
          border-color: $pink-color
    .social-youtube
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: #000
      font-family: $default-font
      font-weight: 700
      font-size: 20px
      line-height: 36px
      text-decoration: none
      transition: color .3s
      margin-top: 10px
      .icon
        color: #FF0000
        font-size: 28px
        line-height: 28px
        margin-right: 8px
      &:hover
        color: #FF0000
    .social-tg
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: #000
      font-family: $default-font
      font-weight: 700
      font-size: 20px
      line-height: 36px
      text-decoration: none
      transition: color .3s
      margin: 10px 0 0 25px
      @media (max-width: 576px)
        margin: 10px 0 0 15px
      @media (max-width: 420px)
        margin: 10px 0 0 60px
      .icon
        color: #0088cc
        font-size: 36px
        line-height: 36px
        margin-right: 5px
      &:hover
        color: #0088cc

/* learning page */
.clearn-main
  padding-bottom: 80px
  overflow: hidden
  @media (max-width: 1200px)
    padding-bottom: 120px
  @media (max-width: 768px)
    padding-bottom: 60px
  @media (max-width: 420px)
    padding: 35px 0
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 38px
    @media (max-width: 420px)
      margin-top: 35px
    &__left-side
      flex: 0 0 65%
      max-width: 65%
      width: 100%
      @media (max-width: 1024px)
        flex: 0 0 55%
        max-width: 55%
      @media (max-width: 768px)
        flex: 0 0 60%
        max-width: 60%
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
      .learn-btn
        margin-top: 20px
        @media (max-width: 1024px)
          margin-top: 30px
        @media (max-width: 420px)
          margin-top: 20px
          padding: 13px 42px
    &__right-side
      flex: 0 0 35%
      max-width: 35%
      width: 100%
      position: relative
      @media (max-width: 1024px)
        flex: 0 0 45%
        max-width: 45%
      @media (max-width: 768px)
        flex: 0 0 40%
        max-width: 40%
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
        margin-bottom: -90px
      @media (max-width: 420px)
        margin-bottom: 0
      .learning-image
        display: block
        width: auto
        position: absolute
        top: calc(-50% - 5px)
        left: 0
        @media (max-width: 1024px)
          max-width: 100%
          top: calc(-50% + 35px)
        @media (max-width: 768px)
          top: calc(-50% + 55px)
        @media (max-width: 640px)
          position: relative
          margin: 0 0 0 auto
          top: calc(-50% + 90px)
        @media (max-width: 420px)
          position: relative
          top: 0
          left: 0
          margin: 0 auto
          max-width: calc(100% + 30px)
  &__title
    font-family: $default-font
    font-weight: 700
    font-size: 37px
    line-height: 48px
    color: #000
    letter-spacing: .02em
    @media (max-width: 1024px)
      font-size: 32px
      line-height: 45px
    @media (max-width: 768px)
      font-size: 30px
      line-height: 42px
    @media (max-width: 420px)
      font-size: 26px
      line-height: 34px




