/* mobile full height fix */
:root {
  --app-height: 100%;
  --header-height: 0;
}

body {
  overflow-x: hidden;
  position: relative;
  height: 100%;
  background-color: #fff;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #F96791;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #6FDE9B;
}
body::-webkit-scrollbar-track {
  background: #F3F5F6;
  border-radius: 0;
}
body {
  scrollbar-face-color: #F96791;
  scrollbar-track-color: #F3F5F6;
}
body.overflow {
  overflow: hidden;
}
body.overflow:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 2;
}

.ccontainer {
  width: 100%;
  margin: 0 auto;
  max-width: 1140px;
  padding: 0 15px;
}

.cheader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s;
}
.cheader .ccontainer {
  max-width: 1440px;
}
.cheader__top {
  padding: 16px 0;
  background: linear-gradient(75.57deg, rgba(250, 97, 115, 0.3) 15.62%, rgba(22, 147, 220, 0.3) 57.16%, rgba(0, 199, 83, 0.3) 94.08%);
}
