.cheader__main .hamburger {
  padding: 0;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  width: 22px;
  height: 32px;
  border-radius: 50%;
  z-index: 20;
  margin: 0 0 0 20px;
}
@media (max-width: 1024px) {
  .cheader__main .hamburger {
    display: block !important;
  }
}
.cheader__main .hamburger.is-active .hamburger-inner {
  background-color: #000;
}
.cheader__main .hamburger.is-active .hamburger-inner::before, .cheader__main .hamburger.is-active .hamburger-inner::after {
  background-color: #000;
}
.cheader__main .hamburger-box {
  width: 22px;
  height: 32px;
  display: inline-block;
  position: relative;
}
.cheader__main .hamburger-inner {
  display: block;
  top: 50%;
  width: 22px;
  height: 2px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-timing-function: ease;
  transition: transform 0.15s;
}
.cheader__main .hamburger-inner::before, .cheader__main .hamburger-inner::after {
  width: 22px;
  height: 2px;
  background-color: #000;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.cheader__main .hamburger-inner::before, .cheader__main .hamburger-inner::after {
  content: "";
  display: block;
}
.cheader__main .hamburger-inner::before {
  top: -10px;
}
.cheader__main .hamburger--slider .hamburger-inner {
  top: 7px;
}
.cheader__main .hamburger--slider .hamburger-inner::before {
  top: 8px;
  left: 0;
  width: 16px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.cheader__main .hamburger--slider .hamburger-inner::after {
  top: 16px;
}
.cheader__main .hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(-2px, 8px, 0) rotate(45deg);
  height: 2px;
